export default {
	data() {
		return {
			// title: 'Hello'
		}
	},
	onLoad() {
		uni.getStorage({
			key: 'language',
			success: function(res) {
				console.log(res.data);
			},
			fail: function() {
				uni.setStorage({
					key: 'language',
					data: 'en',
					success: function() {
						console.log('success');
					}
				});
			}
		});

		uni.getStorage({
			key: 'customer',
			success: function(res) {
				uni.navigateTo({
					url: 'root'
				});
			},
			fail: function() {
				uni.navigateTo({
					url: '../register/wingOn'
				});
			}
		});


	},
	methods: {

	}
}
